import Breakcrum from "@/components/Breakcrum";
import Container from "@/components/Container";
import LikeShare from "@/components/LikeShare";
import Loading from "@/components/Loading";
import SideBarCategory from "@/components/SideBarCategory";
import SideBarLatest from "@/components/SideBarLatest";
import { Calendar } from "@/components/ui/calendar";
import { emptyImage } from "@/images";
import { getAllBlogCategory, getAllBlogs, getBlogBySlug } from "@/services/blogService";
import type { Metadata } from "next";
import Image from "next/image";
import Link from "next/link";
import React from "react";

type Props = {
  params: { slug: string };
};

// ✅ generateMetadata
export async function generateMetadata({ params }: Props): Promise<Metadata> {
  const { slug } = params;

  const post = await getBlogBySlug(slug);

  if (!post?.data?.data) {
    return {
      title: "Bài viết không tồn tại | Indeco VietNam",
      description: "Trang này không tìm thấy hoặc đã bị xoá.",
      robots: { index: false, follow: false },
    };
  }

  const { title, description, image, updated_at, created_at, author, tag } = post.data.data;

  const url = `${process.env.NEXT_PUBLIC_SITE_URL}/blog/${slug}`;

  return {
    title: `${title} | Indeco VietNam`,
    description,
    keywords: [title, "blog", "tin tức", ...(tag || [])],
    alternates: {
      canonical: url,
    },
    openGraph: {
      title,
      description,
      images: [
        {
          url: image || "/default-og.png",
          width: 1200,
          height: 630,
          alt: title,
        },
      ],
      locale: "vi_VN",
      type: "article",
      authors: [author.fullname],
      publishedTime: created_at,
      modifiedTime: updated_at,
    },
    twitter: {
      card: "summary_large_image",
      title,
      description,
      images: [image || "/default-og.png"],
    },
    robots: {
      index: true,
      follow: true,
    },
  };
}

// ✅ BlogPage
const BlogPage = async ({ params }: Props) => {
  const { slug } = params;

  const blog = await getBlogBySlug(slug);
  const categories = await getAllBlogCategory();
  const blogRelated = await getAllBlogs({
    params: { limit: 8, category: blog?.data?.data?.category?.id },
  });

  const { title, description, image, published_at, author, category, tag, body } =
    blog?.data?.data || {};

  return (
    <>
      <Breakcrum title={title || ""} description={description || ""} />
      <Container className="py-10 ">
        {!blog ? (
          <Loading />
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-4 gap-10">
            <div className="col-span-4 md:col-span-3">
              <div className="flex flex-col gap-5">
                <div>
                  <Image
                    src={image || emptyImage}
                    alt="blogImage"
                    width={400}
                    height={400}
                    className="w-full h-auto object-cover rounded-[5px]"
                  />
                </div>
                <div className="space-y-3">
                  <h1 className="text-2xl text-darkColor font-bold">{title}</h1>
                  <p className="text-gray-600 text-base">{description}</p>
                  <div className="flex items-center gap-5">
                    <span>Tác giả: {author?.fullname}</span>
                    <div className="flex items-center gap-2 border-l-2 pl-2 border-gray-300">
                      <Calendar className="w-4 h-4" />
                      <span>Xuất bản: {published_at}</span>
                    </div>
                    <div className="border-l-2 pl-2 border-gray-300 hover:text-dark_brownish hoverEffect">
                      <Link href={`/blog?category=${category?.slug}`}>
                        <span>{category?.title}</span>
                      </Link>
                    </div>
                  </div>
                </div>
                <div>
                  {body ? (
                    <div dangerouslySetInnerHTML={{ __html: body }} />
                  ) : (
                    <span>Đang cập nhật ...</span>
                  )}
                </div>
                <div className="flex items-center flex-wrap gap-2">
                  <span>Tags: </span>
                  {tag?.map((tag: string, index: number) => (
                    <span key={index}>
                      <span className="inline-block bg-gray-100 text-sm text-gray-600 rounded px-2 py-1 mr-2 hover:bg-gray-200">
                        #{tag}
                      </span>
                    </span>
                  ))}
                </div>
                <LikeShare url={`/blog/${slug}`} />
              </div>
            </div>
            <div className="col-span-4 md:col-span-1">
              <SideBarCategory title="Danh mục bài viết" data={categories} />
              <SideBarLatest title="Bài viết liên quan" data={blogRelated} />
            </div>
            <div
              className="col-span-4 w-full h-[300px] rounded-xl"
              style={{
                backgroundImage: `url(https://res.cloudinary.com/dn1tmr9ya/image/upload/v1751880557/indeco/banner_article_img_kgil8o.jpg)`,
                backgroundSize: "cover",
                backgroundRepeat: "no-repeat",
                backgroundPosition: "center",
              }}
            ></div>
          </div>
        )}
      </Container>
    </>
  );
};

export default BlogPage;
